import React, { useState, useEffect } from 'react';
import {
    TextField,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    IconButton,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
    Box
} from '@mui/material';
import { Add as AddIcon, Remove as RemoveIcon } from '@mui/icons-material';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider, DatePicker } from '@mui/x-date-pickers';
import ko from 'date-fns/locale/ko';

const JournalEntryForm = ({ onSubmit }) => {
    const [accounts, setAccounts] = useState([]);
    const [formData, setFormData] = useState({
        description: '',
        date: new Date(),
        details: [{ accountId: '', debitAmount: '0', creditAmount: '0' }]
    });

    useEffect(() => {
        fetchAccounts();
    }, []);

    const fetchAccounts = async () => {
        try {
            const types = ['ASSET', 'LIABILITY', 'EQUITY', 'REVENUE', 'EXPENSE'];
            const accountsList = [];
            
            for (const type of types) {
                const response = await fetch(`/api/accounting/accounts/${type}`);
                const data = await response.json();
                accountsList.push(...data);
            }
            
            setAccounts(accountsList);
        } catch (error) {
            console.error('계정 목록 조회 중 오류 발생:', error);
        }
    };

    const handleAddDetail = () => {
        setFormData(prev => ({
            ...prev,
            details: [...prev.details, { accountId: '', debitAmount: '0', creditAmount: '0' }]
        }));
    };

    const handleRemoveDetail = (index) => {
        setFormData(prev => ({
            ...prev,
            details: prev.details.filter((_, i) => i !== index)
        }));
    };

    const handleDetailChange = (index, field, value) => {
        setFormData(prev => ({
            ...prev,
            details: prev.details.map((detail, i) => 
                i === index ? { ...detail, [field]: value } : detail
            )
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        try {
            const response = await fetch('/api/accounting/journal-entries', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    description: formData.description,
                    date: formData.date.toISOString().split('T')[0],
                    details: formData.details.map(detail => ({
                        account: { id: detail.accountId },
                        debitAmount: parseFloat(detail.debitAmount) || 0,
                        creditAmount: parseFloat(detail.creditAmount) || 0
                    }))
                })
            });

            if (response.ok) {
                setFormData({
                    description: '',
                    date: new Date(),
                    details: [{ accountId: '', debitAmount: '0', creditAmount: '0' }]
                });
                onSubmit();
            }
        } catch (error) {
            console.error('분개 저장 중 오류 발생:', error);
        }
    };

    return (
        <form onSubmit={handleSubmit}>
            <Box sx={{ mb: 2 }}>
                <TextField
                    fullWidth
                    label="적요"
                    value={formData.description}
                    onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
                    sx={{ mb: 2 }}
                />
                
                <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={ko}>
                    <DatePicker
                        label="날짜"
                        value={formData.date}
                        onChange={(newDate) => setFormData(prev => ({ ...prev, date: newDate }))}
                        renderInput={(params) => <TextField {...params} fullWidth />}
                    />
                </LocalizationProvider>
            </Box>

            <TableContainer>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>계정과목</TableCell>
                            <TableCell align="right">차변</TableCell>
                            <TableCell align="right">대변</TableCell>
                            <TableCell align="center">작업</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {formData.details.map((detail, index) => (
                            <TableRow key={index}>
                                <TableCell>
                                    <FormControl fullWidth>
                                        <InputLabel>계정과목</InputLabel>
                                        <Select
                                            value={detail.accountId}
                                            onChange={(e) => handleDetailChange(index, 'accountId', e.target.value)}
                                        >
                                            {accounts.map(account => (
                                                <MenuItem key={account.id} value={account.id}>
                                                    {account.name}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                </TableCell>
                                <TableCell>
                                    <TextField
                                        type="number"
                                        value={detail.debitAmount}
                                        onChange={(e) => handleDetailChange(index, 'debitAmount', e.target.value)}
                                    />
                                </TableCell>
                                <TableCell>
                                    <TextField
                                        type="number"
                                        value={detail.creditAmount}
                                        onChange={(e) => handleDetailChange(index, 'creditAmount', e.target.value)}
                                    />
                                </TableCell>
                                <TableCell align="center">
                                    <IconButton onClick={() => handleRemoveDetail(index)} disabled={formData.details.length === 1}>
                                        <RemoveIcon />
                                    </IconButton>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>

            <Box sx={{ mt: 2, display: 'flex', gap: 2 }}>
                <Button variant="outlined" onClick={handleAddDetail}>
                    <AddIcon /> 항목 추가
                </Button>
                <Button variant="contained" type="submit">
                    저장
                </Button>
            </Box>
        </form>
    );
};

export default JournalEntryForm; 