import React, { useState, useEffect } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Box,
  CircularProgress,
  Alert,
  TextField,
  Button,
  Tabs,
  Tab,
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import axios from 'axios';

const FinancialStatements = () => {
  const [tabValue, setTabValue] = useState(0);
  const [date, setDate] = useState(new Date());
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [balanceSheet, setBalanceSheet] = useState([]);
  const [incomeStatement, setIncomeStatement] = useState([]);
  const [cashFlow, setCashFlow] = useState([]);

  const fetchFinancialStatements = async () => {
    setLoading(true);
    try {
      const [balanceSheetRes, incomeStatementRes, cashFlowRes] = await Promise.all([
        axios.get('/api/accounting/balance-sheet', {
          params: { date: date.toISOString().split('T')[0] },
        }),
        axios.get('/api/accounting/income-statement', {
          params: { date: date.toISOString().split('T')[0] },
        }),
        axios.get('/api/accounting/cash-flow', {
          params: { date: date.toISOString().split('T')[0] },
        }),
      ]);

      setBalanceSheet(balanceSheetRes.data);
      setIncomeStatement(incomeStatementRes.data);
      setCashFlow(cashFlowRes.data);
      setError(null);
    } catch (err) {
      setError('재무제표 데이터를 불러오는 중 오류가 발생했습니다.');
      console.error('Error fetching financial statements:', err);
    }
    setLoading(false);
  };

  useEffect(() => {
    fetchFinancialStatements();
  }, []);

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const handleDateChange = (newDate) => {
    setDate(newDate);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    fetchFinancialStatements();
  };

  const renderTable = (data, columns) => (
    <TableContainer component={Paper}>
      <Table>
        <TableHead>
          <TableRow>
            {columns.map((column) => (
              <TableCell key={column.field} align={column.align || 'left'}>
                {column.headerName}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row) => (
            <TableRow key={row.id}>
              {columns.map((column) => (
                <TableCell key={column.field} align={column.align || 'left'}>
                  {column.valueFormatter
                    ? column.valueFormatter(row[column.field])
                    : row[column.field]}
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" gutterBottom>
        재무제표
      </Typography>

      <Box component="form" onSubmit={handleSubmit} sx={{ mb: 3 }}>
        <LocalizationProvider dateAdapter={AdapterDateFns}>
          <DatePicker
            label="기준일자"
            value={date}
            onChange={handleDateChange}
            renderInput={(params) => <TextField {...params} />}
          />
        </LocalizationProvider>
        <Button
          type="submit"
          variant="contained"
          sx={{ ml: 2 }}
          disabled={loading}
        >
          조회
        </Button>
      </Box>

      {loading ? (
        <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
          <CircularProgress />
        </Box>
      ) : error ? (
        <Alert severity="error">{error}</Alert>
      ) : (
        <>
          <Tabs value={tabValue} onChange={handleTabChange} sx={{ mb: 3 }}>
            <Tab label="대차대조표" />
            <Tab label="손익계산서" />
            <Tab label="현금흐름표" />
          </Tabs>

          {tabValue === 0 && renderTable(balanceSheet, [
            { field: 'accountName', headerName: '계정명' },
            { field: 'amount', headerName: '금액', align: 'right', valueFormatter: (value) => value?.toLocaleString() },
          ])}

          {tabValue === 1 && renderTable(incomeStatement, [
            { field: 'accountName', headerName: '계정명' },
            { field: 'amount', headerName: '금액', align: 'right', valueFormatter: (value) => value?.toLocaleString() },
          ])}

          {tabValue === 2 && renderTable(cashFlow, [
            { field: 'accountName', headerName: '계정명' },
            { field: 'amount', headerName: '금액', align: 'right', valueFormatter: (value) => value?.toLocaleString() },
          ])}
        </>
      )}
    </Box>
  );
};

export default FinancialStatements; 