import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  IconButton,
  Typography,
  Box,
} from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';

const PizzaList = () => {
  const navigate = useNavigate();
  const [pizzas, setPizzas] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchPizzas = async () => {
      try {
        const response = await fetch('/api/pizzas');
        if (!response.ok) {
          throw new Error('피자 목록을 불러오는데 실패했습니다.');
        }
        const data = await response.json();
        setPizzas(data);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchPizzas();
  }, []);

  const handleDelete = async (id) => {
    if (window.confirm('정말로 이 피자를 삭제하시겠습니까?')) {
      try {
        await axios.delete(`http://localhost:8080/api/pizzas/${id}`);
        setPizzas(pizzas.filter(pizza => pizza.id !== id));
      } catch (error) {
        console.error('피자 삭제에 실패했습니다:', error);
      }
    }
  };

  const toggleAvailability = async (id) => {
    try {
      const response = await axios.patch(`http://localhost:8080/api/pizzas/${id}/availability`);
      setPizzas(pizzas.map(pizza => 
        pizza.id === id ? response.data : pizza
      ));
    } catch (error) {
      console.error('피자 상태 변경에 실패했습니다:', error);
    }
  };

  if (loading) return <Typography>로딩 중...</Typography>;
  if (error) return <Typography color="error">{error}</Typography>;

  return (
    <Box sx={{ p: 3 }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '20px' }}>
        <Typography variant="h4">피자 메뉴 관리</Typography>
        <Button
          variant="contained"
          color="primary"
          startIcon={<AddIcon />}
          onClick={() => navigate('/pizzas/new')}
        >
          새 피자 추가
        </Button>
      </div>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>이름</TableCell>
              <TableCell>설명</TableCell>
              <TableCell>가격</TableCell>
              <TableCell>재고</TableCell>
              <TableCell>작업</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {pizzas.map((pizza) => (
              <TableRow key={pizza.id}>
                <TableCell>{pizza.name}</TableCell>
                <TableCell>{pizza.description}</TableCell>
                <TableCell>{pizza.price}원</TableCell>
                <TableCell>{pizza.stock}</TableCell>
                <TableCell>
                  <IconButton 
                    color="error"
                    onClick={() => handleDelete(pizza.id)}
                  >
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default PizzaList;