import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import AccountingDashboard from './components/accounting/AccountingDashboard';
import JournalEntryList from './components/accounting/JournalEntryList';
import LedgerList from './components/accounting/LedgerList';
import TrialBalance from './components/accounting/TrialBalance';
import FinancialStatements from './components/accounting/FinancialStatements';
import AccountingReports from './components/accounting/AccountingReports';
import Navbar from './components/layout/Navbar';
import PizzaList from './components/pizza/PizzaList';
import PizzaForm from './components/pizza/PizzaForm';
import OrderList from './components/order/OrderList';
import InventoryList from './components/inventory/InventoryList';
import Login from './components/auth/Login';
import ApiTest from './components/api/ApiTest';

const theme = createTheme({
  palette: {
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
  typography: {
    fontFamily: [
      'Noto Sans KR',
      'sans-serif',
    ].join(','),
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Navbar />
        <Routes>
          <Route path="/" element={<AccountingDashboard />} />
          <Route path="/accounting" element={<AccountingDashboard />} />
          <Route path="/accounting/dashboard" element={<AccountingDashboard />} />
          <Route path="/accounting/journal" element={<JournalEntryList />} />
          <Route path="/accounting/ledger" element={<LedgerList />} />
          <Route path="/accounting/trial-balance" element={<TrialBalance />} />
          <Route path="/accounting/financial-statements" element={<FinancialStatements />} />
          <Route path="/accounting/reports" element={<AccountingReports />} />
          <Route path="/pizzas" element={<PizzaList />} />
          <Route path="/pizzas/new" element={<PizzaForm />} />
          <Route path="/orders" element={<OrderList />} />
          <Route path="/inventory" element={<InventoryList />} />
          <Route path="/login" element={<Login />} />
          <Route path="/api/home" element={<ApiTest />} />
          <Route path="/api/auth" element={<ApiTest />} />
          <Route path="/api/pizzas" element={<ApiTest />} />
          <Route path="/api/users" element={<ApiTest />} />
          <Route path="/api/actuator" element={<ApiTest />} />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App; 