import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
  AppBar,
  Toolbar,
  Button,
  Box,
  Menu,
  MenuItem,
  IconButton,
} from '@mui/material';
import {
  Restaurant as RestaurantIcon,
  Add as AddIcon,
  ShoppingCart as ShoppingCartIcon,
  Inventory as InventoryIcon,
  Login as LoginIcon,
  Code as CodeIcon,
  AccountBalance as AccountBalanceIcon,
  Assessment as AssessmentIcon,
  Receipt as ReceiptIcon,
  Timeline as TimelineIcon,
} from '@mui/icons-material';

const Navbar = () => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [accountingMenuAnchorEl, setAccountingMenuAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleAccountingMenuClick = (event) => {
    setAccountingMenuAnchorEl(event.currentTarget);
  };

  const handleAccountingMenuClose = () => {
    setAccountingMenuAnchorEl(null);
  };

  return (
    <AppBar position="static">
      <Toolbar>
        <Box sx={{ flexGrow: 1, display: 'flex', gap: 2 }}>
          <Button
            color="inherit"
            component={RouterLink}
            to="/accounting"
            startIcon={<AccountBalanceIcon />}
            onClick={handleAccountingMenuClick}
          >
            회계 관리
          </Button>
          <Menu
            anchorEl={accountingMenuAnchorEl}
            open={Boolean(accountingMenuAnchorEl)}
            onClose={handleAccountingMenuClose}
          >
            <MenuItem component={RouterLink} to="/accounting/dashboard" onClick={handleAccountingMenuClose}>
              대시보드
            </MenuItem>
            <MenuItem component={RouterLink} to="/accounting/journal" onClick={handleAccountingMenuClose}>
              분개장
            </MenuItem>
            <MenuItem component={RouterLink} to="/accounting/ledger" onClick={handleAccountingMenuClose}>
              원장
            </MenuItem>
            <MenuItem component={RouterLink} to="/accounting/trial-balance" onClick={handleAccountingMenuClose}>
              시산표
            </MenuItem>
            <MenuItem component={RouterLink} to="/accounting/financial-statements" onClick={handleAccountingMenuClose}>
              재무제표
            </MenuItem>
            <MenuItem component={RouterLink} to="/accounting/reports" onClick={handleAccountingMenuClose}>
              보고서
            </MenuItem>
          </Menu>

          <Button
            color="inherit"
            component={RouterLink}
            to="/pizzas"
            startIcon={<RestaurantIcon />}
          >
            피자 목록
          </Button>
          <Button
            color="inherit"
            component={RouterLink}
            to="/pizzas/new"
            startIcon={<AddIcon />}
          >
            피자 추가
          </Button>
          <Button
            color="inherit"
            component={RouterLink}
            to="/orders"
            startIcon={<ShoppingCartIcon />}
          >
            주문 관리
          </Button>
          <Button
            color="inherit"
            component={RouterLink}
            to="/inventory"
            startIcon={<InventoryIcon />}
          >
            재고 관리
          </Button>
        </Box>

        <Box sx={{ display: 'flex', gap: 2 }}>
          <IconButton
            color="inherit"
            onClick={handleClick}
            startIcon={<CodeIcon />}
          >
            API 테스트
          </IconButton>
          <Menu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            <MenuItem component={RouterLink} to="/api/home" onClick={handleClose}>
              Home API
            </MenuItem>
            <MenuItem component={RouterLink} to="/api/auth" onClick={handleClose}>
              Auth API
            </MenuItem>
            <MenuItem component={RouterLink} to="/api/pizzas" onClick={handleClose}>
              Pizza API
            </MenuItem>
            <MenuItem component={RouterLink} to="/api/users" onClick={handleClose}>
              User API
            </MenuItem>
            <MenuItem component={RouterLink} to="/api/actuator" onClick={handleClose}>
              Actuator
            </MenuItem>
          </Menu>

          <Button
            color="inherit"
            component={RouterLink}
            to="/login"
            startIcon={<LoginIcon />}
          >
            로그인
          </Button>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar; 