import React, { useState, useEffect } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Box,
  CircularProgress,
  Alert,
} from '@mui/material';
import axios from 'axios';

const JournalEntryList = () => {
  const [entries, setEntries] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchJournalEntries();
  }, []);

  const fetchJournalEntries = async () => {
    try {
      const response = await axios.get('/api/accounting/journal-entries');
      setEntries(response.data);
      setLoading(false);
    } catch (err) {
      setError('분개장 데이터를 불러오는 중 오류가 발생했습니다.');
      setLoading(false);
      console.error('Error fetching journal entries:', err);
    }
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Alert severity="error" sx={{ mt: 2 }}>
        {error}
      </Alert>
    );
  }

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" gutterBottom>
        분개장
      </Typography>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>날짜</TableCell>
              <TableCell>분개번호</TableCell>
              <TableCell>적요</TableCell>
              <TableCell>차변계정</TableCell>
              <TableCell>차변금액</TableCell>
              <TableCell>대변계정</TableCell>
              <TableCell>대변금액</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {entries.map((entry) => (
              <TableRow key={entry.id}>
                <TableCell>{new Date(entry.date).toLocaleDateString()}</TableCell>
                <TableCell>{entry.journalNumber}</TableCell>
                <TableCell>{entry.description}</TableCell>
                <TableCell>{entry.debitAccount?.name}</TableCell>
                <TableCell align="right">{entry.debitAmount?.toLocaleString()}</TableCell>
                <TableCell>{entry.creditAccount?.name}</TableCell>
                <TableCell align="right">{entry.creditAmount?.toLocaleString()}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default JournalEntryList; 