import React, { useState, useEffect } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Box,
  CircularProgress,
  Alert,
  TextField,
  Button,
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import axios from 'axios';

const TrialBalance = () => {
  const [trialBalance, setTrialBalance] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [date, setDate] = useState(new Date());

  const fetchTrialBalance = async () => {
    setLoading(true);
    try {
      const response = await axios.get('/api/accounting/trial-balance', {
        params: {
          date: date.toISOString().split('T')[0],
        },
      });
      setTrialBalance(response.data);
      setError(null);
    } catch (err) {
      setError('시산표 데이터를 불러오는 중 오류가 발생했습니다.');
      console.error('Error fetching trial balance:', err);
    }
    setLoading(false);
  };

  useEffect(() => {
    fetchTrialBalance();
  }, []);

  const handleDateChange = (newDate) => {
    setDate(newDate);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    fetchTrialBalance();
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" gutterBottom>
        시산표
      </Typography>

      <Box component="form" onSubmit={handleSubmit} sx={{ mb: 3 }}>
        <LocalizationProvider dateAdapter={AdapterDateFns}>
          <DatePicker
            label="시산일자"
            value={date}
            onChange={handleDateChange}
            renderInput={(params) => <TextField {...params} />}
          />
        </LocalizationProvider>
        <Button
          type="submit"
          variant="contained"
          sx={{ ml: 2 }}
          disabled={loading}
        >
          조회
        </Button>
      </Box>

      {loading ? (
        <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
          <CircularProgress />
        </Box>
      ) : error ? (
        <Alert severity="error">{error}</Alert>
      ) : (
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>계정코드</TableCell>
                <TableCell>계정명</TableCell>
                <TableCell align="right">차변잔액</TableCell>
                <TableCell align="right">대변잔액</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {trialBalance.map((item) => (
                <TableRow key={item.accountCode}>
                  <TableCell>{item.accountCode}</TableCell>
                  <TableCell>{item.accountName}</TableCell>
                  <TableCell align="right">{item.debitBalance?.toLocaleString()}</TableCell>
                  <TableCell align="right">{item.creditBalance?.toLocaleString()}</TableCell>
                </TableRow>
              ))}
              <TableRow>
                <TableCell colSpan={2} align="right">
                  <strong>합계</strong>
                </TableCell>
                <TableCell align="right">
                  <strong>
                    {trialBalance
                      .reduce((sum, item) => sum + (item.debitBalance || 0), 0)
                      .toLocaleString()}
                  </strong>
                </TableCell>
                <TableCell align="right">
                  <strong>
                    {trialBalance
                      .reduce((sum, item) => sum + (item.creditBalance || 0), 0)
                      .toLocaleString()}
                  </strong>
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </Box>
  );
};

export default TrialBalance; 